import { useRef, useEffect, useMemo } from "react";

const OptimizedDarkVeil = ({
  hueShift = 0,
  noiseIntensity = 0,
  scanlineIntensity = 0,
  speed = 0.5,
  scanlineFrequency = 0,
  warpAmount = 0,
  resolutionScale = 0.5, // Reduced default resolution
}) => {
  const canvasRef = useRef(null);
  const animationRef = useRef(null);
  const contextRef = useRef(null);
  const timeRef = useRef(0);

  // Memoize gradient colors to avoid recalculation
  const gradientColors = useMemo(() => [
    `hsl(${120 + hueShift}, 70%, 20%)`, // Green base
    `hsl(${240 + hueShift}, 70%, 15%)`, // Blue base
    `hsl(${180 + hueShift}, 60%, 10%)`, // Cyan base
    'rgba(0, 0, 0, 0.8)'
  ], [hueShift]);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const parent = canvas.parentElement;
    const ctx = canvas.getContext('2d', { alpha: false });
    contextRef.current = ctx;

    // Optimize canvas settings
    ctx.imageSmoothingEnabled = false;
    ctx.globalCompositeOperation = 'source-over';

    const resize = () => {
      const rect = parent.getBoundingClientRect();
      const width = Math.floor(rect.width * resolutionScale);
      const height = Math.floor(rect.height * resolutionScale);
      
      canvas.width = width;
      canvas.height = height;
      canvas.style.width = rect.width + 'px';
      canvas.style.height = rect.height + 'px';
    };

    const drawFrame = () => {
      if (!ctx || !canvas.width || !canvas.height) return;

      const { width, height } = canvas;
      const time = timeRef.current;

      // Clear canvas
      ctx.fillStyle = 'rgba(0, 0, 0, 1)';
      ctx.fillRect(0, 0, width, height);

      // Create animated gradient
      const gradient = ctx.createRadialGradient(
        width * (0.3 + Math.sin(time * 0.001) * 0.1),
        height * (0.4 + Math.cos(time * 0.0015) * 0.1),
        0,
        width * 0.6,
        height * 0.6,
        Math.max(width, height) * 0.8
      );

      gradient.addColorStop(0, gradientColors[0]);
      gradient.addColorStop(0.3, gradientColors[1]);
      gradient.addColorStop(0.7, gradientColors[2]);
      gradient.addColorStop(1, gradientColors[3]);

      ctx.fillStyle = gradient;
      ctx.fillRect(0, 0, width, height);

      // Add subtle warp effect
      if (warpAmount > 0) {
        const imageData = ctx.getImageData(0, 0, width, height);
        const data = imageData.data;
        
        // Simple warp effect (optimized)
        for (let y = 0; y < height; y += 4) { // Skip pixels for performance
          for (let x = 0; x < width; x += 4) {
            const index = (y * width + x) * 4;
            const warpX = Math.sin(y * 0.01 + time * 0.001) * warpAmount * 2;
            const warpY = Math.cos(x * 0.01 + time * 0.001) * warpAmount * 2;
            
            if (index < data.length - 4) {
              data[index] = Math.min(255, data[index] + warpX);
              data[index + 1] = Math.min(255, data[index + 1] + warpY);
            }
          }
        }
        
        ctx.putImageData(imageData, 0, 0);
      }

      // Add noise effect
      if (noiseIntensity > 0) {
        ctx.globalAlpha = noiseIntensity * 0.1;
        ctx.fillStyle = `rgba(${Math.random() * 50}, ${Math.random() * 50}, ${Math.random() * 50}, 0.1)`;
        ctx.fillRect(0, 0, width, height);
        ctx.globalAlpha = 1;
      }

      // Add scanlines
      if (scanlineIntensity > 0 && scanlineFrequency > 0) {
        ctx.globalAlpha = scanlineIntensity * 0.3;
        ctx.strokeStyle = 'rgba(0, 255, 0, 0.1)';
        ctx.lineWidth = 1;
        
        for (let y = 0; y < height; y += scanlineFrequency * 10) {
          ctx.beginPath();
          ctx.moveTo(0, y);
          ctx.lineTo(width, y);
          ctx.stroke();
        }
        ctx.globalAlpha = 1;
      }
    };

    const animate = (timestamp) => {
      timeRef.current = timestamp * speed;
      drawFrame();
      animationRef.current = requestAnimationFrame(animate);
    };

    // Setup
    resize();
    window.addEventListener('resize', resize);
    animationRef.current = requestAnimationFrame(animate);

    return () => {
      window.removeEventListener('resize', resize);
      if (animationRef.current) {
        cancelAnimationFrame(animationRef.current);
      }
    };
  }, [gradientColors, speed, warpAmount, noiseIntensity, scanlineIntensity, scanlineFrequency, resolutionScale]);

  return (
    <canvas
      ref={canvasRef}
      className="w-full h-full block"
      style={{
        imageRendering: 'pixelated',
        transform: 'translateZ(0)',
        backfaceVisibility: 'hidden',
      }}
    />
  );
};

export default OptimizedDarkVeil;